// call animation function below
const animation = () => {

  const timelineProgress = (position) => {
      console.log(`[custom] animation ${position}`);
  }

  const tl = new TimelineMax(
    {
      repeat:1,
      yoyo:false,
      repeatDelay: 4,
      onStart:timelineProgress,
      onStartParams:["start"],
      onComplete:timelineProgress,
      onCompleteParams:["end"]
    }
  );

    // CONTROL MODULE FUNCTIOn
    ControlModule(tl);

  tl.addLabel("frame01")
  .to(".f1", 0.5, {opacity: 1 }, 0)
  .to(".f1_c1", 0.5, {opacity: 0 }, 1.5)
  .to(".f1", 0.5, {opacity: 0 })

  .addLabel("frame02")
  .to(".f2_c1", 0.5, {opacity: 1 }, "frame02-=0.75")
  .to(".f2_bg", 0.5, {opacity: 1 }, "frame02-=1")
  .to(".vf_logo", 0.5, {left:"+=57" }, "frame02-=1")
  .to(".f2_c1, .vf_toggle", 0.5, {opacity: 0 }, "+=1.5")
  .to(".vf_logo", 0.25, {opacity: 0}, "-=0.5")

  .addLabel("frame03")
  .set(".rhombus", {opacity: 1, top:38, right:80, borderRadius: 26})
  .to(".rhombus", 0.5, {
    ease: Power1.easeInOut,
    top: "+=5",
    boxShadow:"0px 0px 15px 15px #E30613",
    rotation: "+=405deg",
    transformOrigin: "900px 3px"
  })
  .to(".rhombus", 0.1, {y:5, x:15, boxShadow: "0px 0px 0px 0px"}, "-=0.4")

  .to(".f3_vf_logo", 0.25, {opacity: 1}, "-=0.5")
  .to(".f3_c1", 0.5, {opacity: 1 })
  .to(".f3_bg", 0.5, {opacity: 1 }, "-=0.5")
  .to(".f3_tcs", 0.5, {opacity: 1 }, "-=0.5")
  .to(".f3_c1", 0.5, {opacity: 0 }, "+=2")

  .addLabel('frame04')
  .to(".f4", 0.5, {opacity: 1 })
  .to(".f4", 0.5, {opacity: 0 }, "+=2")

  .addLabel("frame05")
  .staggerTo(".f5", 0.5, {opacity: 1 }, 1, "frame05")

  .addLabel("frame06")
  .to(".cta", 0.5, {opacity: 1 }, "frame06")
  .to(".f5_c1", 0.5, {y: -5 }, 1, "frame06")
  .to(".f6_c2", 0.5, {opacity: 1 }, "frame06")
  .to(".vf_cta", 0.5, {opacity: 1 }, "frame06")

  console.log(`[custom] loop duration: ${tl.duration()}s`);
  console.log(`[custom] total duration: ${tl.totalDuration()}s`);

  // tl.seek("frame02+=1")
  // .pause();


}
